.container {
    display: flex;
    flex: 1;
    flex-direction: column;
    padding-left: 20px;
    padding-right: 20px;
}

a:hover, a:visited, a:link, a:active
{
    color: inherit;
    text-decoration: none;
}

p {
    font-size: 18px;
}

html, body{
    height:100%;
  }  

body {
    display: flex;
    flex-direction: column;
    font-family: 'Helvetica Neue';
}

#root {
    display: flex;
    flex: 1;
    flex-direction: column;
}