.hero {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10vh;
    margin-bottom: 10vh;
    margin-left: 20vw;
    margin-right: 20vw;
}

.hero-container {
    display: flex;
    flex-direction: row;
}

.stats {
    display: flex;
    flex-direction: row;
    text-align: center;
    width: 100%;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 100px;
    margin-right: 100px;
    margin-left: 100px;
}